import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { withStyles, Paper, Box } from "@material-ui/core";
import { DESK_WIDTH, DESK_HEIGHT } from '../helpers/constants';
import { Resizer } from '../../../components';

const DEFAULT_IMAGE = 'data:image/png;base64,';

const styles = () => ({
    screen: {
        position: 'relative',
        margin: '0 auto',
        width: '100%',
        height: '100%'
    },
    image: {
        display: 'block',
        width: '100%',
        height: '100%',
    },
    note: {
        width: '100%',
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        flexWrap: 'wrap',
    }
});

class Screen extends PureComponent {

    render() {
        const { classes, image } = this.props;
        return (
            <Resizer
                k={DESK_HEIGHT/DESK_WIDTH}
            >
                <Paper
                    elevation={5}
                    square={true}
                    className={classes.screen}
                >
                    {
                        image && image.length ? <img
                                src={image || DEFAULT_IMAGE}
                                className={classes.image}
                                alt="gamehub"
                            /> :
                            (
                                <Box
                                    className={classes.note}
                                >
                                    The game is going to start
                                </Box>
                            )
                    }
                </Paper>
            </Resizer>
        )
    }
}

export default connect(
    store => {
        return {
            image: store.crocodile.room.image,
        }
    },
)(withStyles(styles)(Screen));